html {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}
body {
  line-height: 1.6;
  font-family: -apple-system-font, "Helvetica Neue", sans-serif;
}
* {
  margin: 0;
  padding: 0;
}
a img {
  border: 0;
}
a {
  text-decoration: none;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}
.title {
  text-algin: center;
  font-size: 18px;
  padding: 15px;
}
.main-picture {
  padding: 15px;
}
.main-picture img {
  width: 100%;
}
.content {
  padding: 15px;
}



