
@import '~vue-swipe/dist/vue-swipe.css';
.my-swipe {
  height: 200px;
  color: #fff;
  font-size: 30px;
  text-align: center;
}
.notice {
    height: 30px;
    width: 100%;
    padding: 5px;
    padding-to: 10px;
}
.notice > div > img {
    width: 25px;
    padding-top:5px;
    padding-left: 15px;
}
.notice > div {
    float: left;
}
.notice .text {
    color: red;
    font-size: 18px;
    padding-left: 15px;
}
.modules {
    float: left;
    width: 33.3333%;
    margin-top: 10px
}
.modules .icon {
    text-align: center;
}
.modules .icon > img {
    width: 50px;
}
.modules .title {
    text-align: center;
    color: #000;
}
.m-img {
  padding-bottom: 33%;
  display: block;
  position: relative;
  max-width: 100%;
  background-size: cover;
  background-position: center center;
  cursor: pointer;
  border-radius: 2px;
}
.m-title {
  color: #fff;
  text-align: center;
  text-shadow: 0 0 2px rgba(0, 0, 0, .5);
  font-weight: 500;
  font-size: 16px;
  position: absolute;
  left: 0;
  right: 0;
  width: 100%;
  text-align: center;
  top: 50%;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
}
.m-time {
  font-size: 12px;
  padding-top: 4px;
  border-top: 1px solid #f0f0f0;
  display: inline-block;
  margin-top: 5px;
}



