html {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}
body {
  line-height: 1.6;
  font-family: -apple-system-font, "Helvetica Neue", sans-serif;
}
* {
  margin: 0;
  padding: 0;
}
a img {
  border: 0;
}
a {
  text-decoration: none;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}
body {
  background-color: #fbf9fe;
}
a {
  color: #C0C0C0;
}
.router-view {
  width: 100%;
  height: 100%;
  top: 46px;
  position: absolute;
}
.bottom-menu {
  border-top: 1px solid #ccc;
  background-color: white;
  height: 55px;
}
.bottom-menu div {
  float: left;
  padding-left: 20px;
}
.bottom-menu img {
  padding-top: 6px;
  width: 40px;
  height: 40px;
}
.bottom-menu .label {
  font-size: 18px;
  padding: 12px;
  color: #000;
}



