html {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}
body {
  line-height: 1.6;
  font-family: -apple-system-font, "Helvetica Neue", sans-serif;
}
* {
  margin: 0;
  padding: 0;
}
a img {
  border: 0;
}
a {
  text-decoration: none;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}
.title {
  text-align: center;
  font-size: 18px;
  padding: 5px;
}
.main-pic {
  text-align: center;
  padding: 15px;
}
.main-pic img {
  width: 30%;
}
.content {
  padding: 15px;
}
.contract {
  width: 70%;
  height: 50px;
  background-color: #fea614;
  text-align: center;
  color: white;
  font-size: 17px;
  line-height: 3em;
  margin-left: 15%;
}
.contract > img {
  width: 25px;
}



